.optForm {
    width: 100%;
  .optForm__form {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
    margin-top: 20px;
    @media (max-width: 1000px) {
      flex-direction: column;
      align-items: center;
    }
    input {
      max-width: 450px;
      width: 100%;
      border: 0;
      padding: 10px;
      height: 70px;
      box-sizing: border-box;
    }
    button {
      display: flex;
      align-items: center;
      height: 70px;
      background: #e50914;
      color: white;
      text-transform: uppercase;
      padding: 0 32px;
      font-size: 26px;
      border: 0;
      cursor: pointer;
      img {
        margin-left: 10px;
        filter: brightness(0) invert(1);
        width: 24px;
        @media (max-width: 1000px) {
          width: 16px;
        }
      }
      &:hover {
        background: #f40612;
      }
      @media (max-width: 1000px) {
        height: 50px;
        font-size: 16px;
        margin-top: 20px;
        font-weight: bold;
      }
    }
  }
  .optForm__text {
    font-size: 19.2px;
    color: white;
    text-align: center;
    margin-top: 15px;
    @media (max-width: 600px) {
      font-size: 16px;
      line-height: 22px;
    }
  }
}
